.App {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Anton", "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#mode-changer #triangle-topleft[mode~="dark"] {
  border-top: 60px solid #ffff;
}
#mode-changer #triangle-topleft[mode~="light"] {
  border-top: 60px solid #303030;
}

#overlay i:hover[mode~="dark"] {
  color: #303030;
}
#overlay i[mode~="dark"] {
  color: #fffb00;
}

#mode-changer[mode~="dark"] {
  position: absolute;
  width: 100%;
  height: 100%;
  color: #ffff;
  background: #303030;
}

#mode-changer[mode~="light"] {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #ffff;
  color: #303030;
}

.square[mode~="light"] {
  height: 20px;
  width: 20px;
  background-color: #303030;
  display: inline-block;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  margin-left: 5px;
}
.square[mode~="dark"] {
  height: 20px;
  width: 20px;
  background-color: #ffff;
  display: inline-block;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  margin-left: 5px;
}

.bounce-7 {
  animation-name: bounce-7;
  animation-timing-function: cubic-bezier(0.28, 0.84, 0.42, 1);
}
@keyframes bounce-7 {
  0% {
    transform: scale(1, 1) translateY(0);
  }
  10% {
    transform: scale(1.1, 0.9) translateY(0);
  }
  30% {
    transform: scale(0.9, 1.1) translateY(-20px);
  }
  50% {
    transform: scale(1.05, 0.95) translateY(0);
  }
  57% {
    transform: scale(1, 1) translateY(-7px);
  }
  64% {
    transform: scale(1, 1) translateY(0);
  }
  100% {
    transform: scale(1, 1) translateY(0);
  }
}

a {
  text-decoration: none;
  color: rgb(226, 212, 212);
}

a:hover {
  text-decoration: none;
  color: #000000;
  /* color: #FFFFFFFF; */
}
