#mode-changer {
  position: absolute;
  width: 100%;
  height: 100%;
}
#overlay i:hover{
    color:#fffb00;
}
#overlay i{
    color:white;
}

#mode-changer #triangle-topleft {
  width: 0;
  height: 0;
  border-top: 60px solid #303030;
  border-left: 60px solid transparent;
  position: absolute;
}

#mode-changer #triangle-topleft,
#overlay {
  position: absolute;
  color: white;
  right: 0;
  font-size: 30px;
}
